body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body{
  background-color: #2f7a8f; 
  font-family: "Whitney Cond A";
  color: white;

}
.bigBox{
  text-align: center;
}
.div1, .div2{
  margin: 20px;
  line-height: 40px;
  list-style-type: none;
  display: inline;
}
.div3{
  margin: 20px;
  line-height: 40px;
  list-style-type: none;
}
table {
  border-collapse: separate;
  border-spacing: 25px 20px;
}
td, th {
  text-align: center;
  font-size: 25px
}
.table{
  display: flex;
  justify-content: center;
}

img {
  height: 90px;
  width: 90px;
  position: relative;
  -webkit-animation: floatBubble 3s normal ease-in-out;
          animation: floatBubble 3s normal ease-in-out;
}

@-webkit-keyframes floatBubble {
  100% {
    top: 300px;
  }
  0% {
    top:900px;
  }
  100% {
    left: 100px;
  }
  
  to { -webkit-transform: scale(9.1); transform: scale(9.1); }
  0%   { opacity: -7; }
  100% { opacity: 1; }
}

@keyframes floatBubble {
  100% {
    top: 300px;
  }
  0% {
    top:900px;
  }
  100% {
    left: 100px;
  }
  
  to { -webkit-transform: scale(9.1); transform: scale(9.1); }
  0%   { opacity: -7; }
  100% { opacity: 1; }
}

button{
  border-radius: 8px;
  height: 30px;
  color: #2f7a8f;
  font-size: 15px;
}
.weatherDiv{
  display: flex;
  justify-content: center;
